<template>
  <div class="home">
    <div class="ban_sear">
      <input type="text" placeholder="请输入" @change="aa()" v-model="val" />
      <span>搜索</span>
      <div class="sear" v-if="bool">
        <p @click="close()" class="close">关闭</p>
        <router-link :to="{path:'/xq',query:{ids:item.comic_name}}" tag="p" v-for="(item,i) in seararr" :key="i">{{item.comic_name}}</router-link>
      </div>
    </div>
    <div class="block">
      <el-carousel height="10rem">
        <el-carousel-item v-for="item in banner" :key="item">
          <img class="small" :src="item" >
        </el-carousel-item>
      </el-carousel>
    </div>
    <div class="hot tbox">
      <router-link :to="{path:'/ph',query:{ln:'dushi'}}" tag="h3">热门佳作</router-link>
      <ul>
        <router-link :to="{path:'/xq',query:{ids:item.tit}}" tag="li" v-for="(item,i) in hotarr" :key="i">
          <img :src="item.src" :alt="item.tit" />
          <p>{{item.tit}}</p>
        </router-link>
      </ul>
    </div>
    <div class="tj tbox">
      <router-link :to="{path:'/ph',query:{ln:'shenmo'}}" tag="h3">小编推荐</router-link>
      <ul>
        <router-link :to="{path:'/xq',query:{ids:item.tit}}" tag="li" v-for="(item,i) in tjarr" :key="i">
          <img :src="item.src" :alt="item.tit" />
          <p>{{item.tit}}</p>
        </router-link>
      </ul>
    </div>
    <div class="hg tbox">
      <router-link :to="{path:'/ph',query:{ln:'rexue'}}" tag="h3">热情澎湃</router-link>
      <ul>
        <router-link :to="{path:'/xq',query:{ids:item.tit}}" tag="li" v-for="(item,i) in hgarr" :key="i">
          <img :src="item.src" :alt="item.tit" />
          <p>{{item.tit}}</p>
        </router-link>
      </ul>
    </div>
    <div class="bz tbox">
      <router-link :to="{path:'/ph',query:{ln:'kongbu'}}" tag="h3">恐怖专区</router-link>
      <ul>
        <router-link :to="{path:'/xq',query:{ids:item.tit}}" tag="li" v-for="(item,i) in bzarr" :key="i">
          <img :src="item.src" :alt="item.tit" />
          <p>{{item.tit}}</p>
        </router-link>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: "home",
  components: {
    
  },
  data() {
    return {
      val: "",
      banner:["img/icons/banner1.png","img/icons/banner2.png","img/icons/banner3.png","img/icons/banner4.png"],
      bool: false,
      hotarr: [],
      tjarr: [],
      hgarr: [],
      bzarr: [],
      seararr:[]
    };
  },
  methods: {
    aa() {
      this.bool = true;
      var that = this;
      var ids = this.val;
      var urls ="https://www.manhuatai.com/api/getsortlist/?search_key="+ids;
      this.axios.get(urls).then(function(res) {
          var shuju = res.data.data;
          that.seararr=shuju
      });
    },
    close() {
      this.bool = false;
      this.val="";
    },
    getdata(url, arr) {
      var that = this;
      that.axios.get(url, arr).then(function(res) {
        var shuju = res.data;
        var hres = shuju.match(/class="thumbnail"((?!alt=).)*alt=/gi);
        hres = hres.join();
        hres = hres
          .match(/data-src((?!(jpg)|(png)|(JPG)).)+((jpg)|(png)|(JPG))/gi)
          .join();
        hres = hres
          .match(/\/\/((?!(jpg)|(png)|(JPG)).)+((jpg)|(png)|(JPG))/gi)
          .join()
          .replace(/\//gi, "")
          .replace(/commh/gi, "com/mh/")
          .split(",");
        var tits = shuju
          .match(/class="title">([\u4E00-\u9FFF]|[a-zA-Z]|[0-9])+/gi)
          .join();
        tits = tits
          .match(/>([0-9]|[a-zA-Z]|[\u4E00-\u9FFF])+/gi)
          .join()
          .replace(/<|>|\//gi, "")
          .split(",");
        for (var i = 0; i < 6; i++) {
          var obj = {};
          obj.tit = tits[i];
          obj.src = "https://" + hres[i] + "-480x640.webp";
          arr.push(obj);
        }
      });
    }
  },
  created() {
    var that = this;
    var arr = ["dushi", "shenmo", "rexue", "kongbu"];
    var brr = [that.hotarr, that.tjarr, that.hgarr, that.bzarr];
    for (var i = 0; i < arr.length; i++) {
      var urls = "https://m.manhuatai.com/sort/" + arr[i] + ".html";
      that.getdata(urls, brr[i]);
    }
  }
};
</script>
