<template>
  <div id="app">
    <div class="banner" v-if="bool">
       <router-link to="/" ><img src="../public/img/icons/home_03.png" alt="首页" ref="one"></router-link>  
       <router-link to="/about" ><img src="../public/img/icons/book.png" alt="书架"></router-link>
