<script>
export default {
  name: 'app',
  components: {
   
  },
  data(){
    return {
      bool:true
    }
  },
  watch:{
    $route(e){
      var that = this
      if(e.name=="home"||e.name=="about"||e.name=="ph"){
        that.bool = true
      }else{
        that.bool = false
      }
    }
  }
}
</script>

